import React from 'react';
import styled from 'styled-components';
import { Colors, Mixins } from '../shared/typography';
import Logo from '../images/scandit_logo.svg';

export default function ScanningBy({ hidden }) {
  return (
    <ScanLogoContainer hidden={hidden}>
      <div className="scan-logo__title">Scanning by&nbsp;</div>
      <img src={Logo} width="80px" height="12px" fill={Colors.darkGrey} alt="Scandit Logo" />
    </ScanLogoContainer>
  );
}

const ScanLogoContainer = styled.div`
  ${Mixins.flexCenter}
  height: 36px;
  background-color: ${Colors.backgroundLight};
  position: relative;
  transition: 0.3s;
  opacity: ${({ hidden }) => (hidden ? 0 : 1)};
  background-color: ${({ hidden }) => (hidden ? Colors.white : Colors.backgroundLight)};

  .scan-logo__title {
    ${Mixins.helvetica}
    color: ${Colors.darkGrey};
    size: 16px;
    line-height: 24px;
    font-weight: 500px;
    letter-spacing: 0.48px;
  }
`;
