import React, { forwardRef } from 'react';
import styled from 'styled-components';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import IconButton from '@material-ui/core/IconButton';
import Loader from './Loader';
import CloseIcon from '../images/close_icon.svg';

import { Sizes, Breakpoints, Mixins } from '../shared/typography';

const Transition = forwardRef((props, ref) => {
  const { timeout, ...rest } = props;

  return (
    <Slide direction="up" timeout={{ enter: 400, exit: 800 }} ref={ref} {...rest} />
  )
});

export default function ResultsDialog(props) {
  const { isOpen, onClose, data, isLoading, Header, Content, hideBackdrop, additionalStyles = '' } = props;

  return (
    <div>
      <StyledDialog
        open={isOpen}
        additionalStyles={additionalStyles}
        TransitionComponent={Transition}
        keepMounted
        onClose={onClose}
        isLoading={isLoading}
        hideBackdrop={hideBackdrop}
        showResults={!!data}
        PaperProps={{ elevation: 1 }}
      >
        <StyledDialogTitle>
          <CloseButton aria-label="close" onClick={onClose}>
            <img src={CloseIcon} alt="X" fontSize="small" />
          </CloseButton>
          {Header && <Header data={data} />}
        </StyledDialogTitle>
        <StyledDialogContent isLoading={isLoading}>
          {isLoading && <Loader />}
          {!isLoading && Content && <Content data={data} />}
        </StyledDialogContent>
      </StyledDialog>
    </div>
  );
}

const desktopDialogBackdropPosition = `
  top: calc(${Sizes.contentMargin} * 2) !important;
  left: calc((100vw - ${Sizes.desktopMaxWidth}) / 2) !important;
  right: calc((100vw - ${Sizes.desktopMaxWidth}) / 2) !important;
`;

/* eslint-disable indent */
const StyledDialog = styled(Dialog)`
  @media(${Breakpoints.min}) {
    &.MuiDialog-root {
      ${desktopDialogBackdropPosition}
    }

    .MuiBackdrop-root {
      ${desktopDialogBackdropPosition}
    }
  }

  .MuiDialog-paper {
    margin: 0px;
    margin-top: ${Sizes.dialogMargin};
    max-height: calc(100% - ${Sizes.dialogMargin});
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    transition: height linear 0.5s;
    width: 100vw;
    max-width: ${Sizes.maxWidth};

    height: ${({ isLoading, showResults }) => {
      if (!isLoading && !showResults) return 'initial';
      if (isLoading) return '50%';

      return `calc(100% - ${Sizes.dialogMargin})`;
    }};

    @media(${Breakpoints.min}) {
      margin-top: ${Sizes.desktopDialogMargin};
      max-height: calc(100% - ${Sizes.contentMargin});
      max-width: ${Sizes.desktopMaxWidth};

      height: ${({ isLoading, showResults }) => {
        if (!isLoading && !showResults) return '232px';
        if (isLoading) return '50%';

        return `calc(100% - ${Sizes.contentMargin})`;
      }};
    }
  }
  .MuiDialog-scrollPaper {
    align-items: flex-end;
  }

  ${({ additionalStyles }) => additionalStyles}
`;
/* eslint-enable indent */

const StyledDialogTitle = styled(DialogTitle)`
  &.MuiDialogTitle-root {
    padding: 0px;
    word-break: break-word;
  }
`;

const StyledDialogContent = styled(DialogContent)`
  &.MuiDialogContent-root {
    word-break: break-word;

    ${({ isLoading }) => (isLoading ? Mixins.flexCenter : '')}
  }
`;

const CloseButton = styled(IconButton)`
  &.MuiIconButton-root {
    margin-top: 10px;
    margin-left: 10px;
    padding: 6px;
  }
`;
