import React, { useState } from 'react';
import styled from 'styled-components';
import { ToastContainer, toast } from 'react-toastify'; // toast

import { Colors, Mixins } from '../shared';

export const Modal = ({ numberOfItems, name, image, customImageSize, setShowModal, setReorderes }) => {
  const [counter, setCounterState] = useState(0);

  // const increment = () => {
  //   setCounterState(counter + 1);
  // };

  // const decrement = () => {
  //   setCounterState(counter - 1);
  // };
  // const closeModal = () => {
  //   setShowModal(prev => !prev);
  // };

  const confirmOrder = () => {
    setShowModal(prev => !prev);
    setReorderes(true);
    toast.success(`Product ${name} has been re-ordered with amount of ${counter}`);
  };

  const declineOrder = () => {
    setShowModal(prev => !prev);
    toast.error(`You declined an order of ${name}`);
  };

  const changeAmount = (event) => {
    setCounterState(event.target.value);
  };

  const clearInput = () => {
    setCounterState('');
  };

  const tooHightAmount = counter > numberOfItems;
  console.log('tooHightAmount', tooHightAmount);
  console.log('counter', counter);
  return (
    <Background>
      <ModalWrapper>
        <ItemName>{name}</ItemName>
        <Image src={image} imgSize={customImageSize} />

        <Info>
          <Text>Amount: </Text>
          <AmountInput type="number" value={counter} onClick={clearInput} onChange={changeAmount} /><Text> /{numberOfItems}</Text>
        </Info>
        {tooHightAmount && <ErrorText>Not enought items !</ErrorText>}
        <Footer>
          <ConfirmButton type="submit" disabled={tooHightAmount || counter == 0} onClick={confirmOrder}>Confirm</ConfirmButton>
          <DeclineButton type="submit" onClick={declineOrder}>Decline</DeclineButton>
        </Footer>

      </ModalWrapper>
      <ToastContainer pauseOnFocusLoss={false} hideProgressBar />
    </Background>
  );
};

const Background = styled.div`
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalWrapper = styled.div`
  @media (min-width: 300px) {
    width: 70vw;
    height: 60vh;
  }
  @media (min-width: 600px) {
    width: 25vw;
    height: 55vh;
  }
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
  background: #fff;
  color: #000;
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 10;
  border-radius: 10px;
  padding: 10px;
  justify-content: space-around;
  align-items: center;
`;

const Image = styled.img`
  width: ${props => props.imgSize}px;
  height: ${props => props.imgSize}px;
  border: 1px solid ${Colors.black};
`;

const ItemName = styled.h2`
  ${Mixins.helvetica}
  margin: 0px;
  font-weight: bold;
  padding: 8px 0;
  color: ${Colors.black};
  text-align: center;
`;

const Info = styled.div`
  margin-top: 20px;
  display:flex;
  justify-content: space-beetwen;
`;

const AmountInput = styled.input`
  color: ${Colors.black};
  text-align: center;
  width: 40px;
  fonst-size: 18px;
`;

const Text = styled.p`
  margin: 0px;
  font-size: 14px;
  line-height: 20px;
  font-weight: bold;
  color: ${Colors.black};
  font-size: 18px;
`;

const ErrorText = styled.p`
  margin: 0px;
  font-size: 14px;
  padding: 6px 0;
  line-height: 20px;
  color: #E74C3C;
  font-weight: bold;
`;

const ConfirmButton = styled.button`
  background-color: #07B10C;
  border-radius: 5px;
  padding: 10px;
  font-weight: bold;
  :disabled {
    background-color: ${Colors.disabledButton};
  }
`;

const DeclineButton = styled.button`
  background-color: #E74C3C;
  border-radius: 5px;
  padding: 10px;
  font-weight: bold;
`;

const Footer = styled.div`
  display: flex;
  justify-content: space-around;
  width: 100%;
`;
