import styled from 'styled-components';
import { Mixins, Colors } from '../shared/typography';

export const Content = styled.div`
  margin-top: 24px;
  margin-bottom: 16px;
  transition: 0.3s;

  height: ${({ isExpanded }) => (!isExpanded ? '100px' : '210px')};
  overflow: hidden;
`;

export const Step = styled.div`
  ${Mixins.helvetica}
  color: ${Colors.darkGrey};
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0px;
  line-height: 19px;
`;

export const Title = styled.div`
  ${Mixins.helvetica}
  color: ${Colors.darkGrey};
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0px;
  line-height: 36px;
`;

export const Link = styled.div`
  color: ${Colors.linkColor};
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0px;
  line-height: 20px;
  cursor: pointer;
`;

export const Info = styled.div`
  color: ${Colors.infoColor};
  font-size: 16px;
  letter-spacing: 0px;
  line-height: 24px;

`;

export const Divider = styled.div`
  background: ${Colors.dividerColor};
  height: 1px;
  width: 100%;
  margin-bottom: 24px;
  margin-top: 24px;
`;
