import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { Layout, HomePage } from '@scandit/react-toolkit';

import { ScanPage } from './components/ScanPage';
import { ResultsPage } from './components/ResultsPage';
import { HomeFooter as Footer } from './components/HomeFooter';

const appVariantName = process.env.REACT_APP_PRODUCT_TYPE.toLocaleLowerCase();

const homeParams = {
  title: `Re-ordering ${appVariantName} products Scanning Demo`,
  description: 'Scan the barcode of an item to show product information and re-order',
  scanText: 'Start Scanning',
  scanLink: '/scan-product',
  Footer
};

function AppContent() {
  return (
    <Layout>
      <Switch>
        <Route exact path="/">
          <HomePage {...homeParams} />
        </Route>
        <Route path="/home">
          <HomePage {...homeParams} />
        </Route>
        <Route path="/scan-product">
          <ScanPage />
        </Route>
        <Route path="/result/:id">
          <ResultsPage />
        </Route>
        <Redirect to="/" />
      </Switch>
    </Layout>
  );
}

export default AppContent;
