import React, { useState } from 'react';
import { useParams, Link, useHistory } from 'react-router-dom';
import ButtonBase from '@material-ui/core/ButtonBase';
import { ToastContainer } from 'react-toastify'; // toast
import 'react-toastify/dist/ReactToastify.css';
import styled from 'styled-components';

import ScanIcon from '../images/scan_icon.svg';
import { RelatedProductInfo } from './RelatedProductInfo';
import { ProductHeader } from './ProductHeader';
import { ProductDescription } from './ProductDescription';
import { Modal } from './Modal';
import { Colors, Mixins } from '../shared';
import { getProductById } from '../services/product-service';

export function ResultsPage() {
  const [isReordered, setReorderes] = useState(false); // setReorderes
  const [showModal, setShowModal] = useState(false);
  const { id } = useParams();
  const selectedItem = getProductById(id);
  const history = useHistory();
  console.log('selectedItem', selectedItem);
  // const showToast = () => {
  //   toast.success(`Product ${selectedItem.name} has been re-ordered`);
  //   setReordered(true);
  // };

  const openModal = () => {
    setShowModal(prev => !prev);
  };

  const redirectHomePage = () => history.push('/');

  return (
    <Container>
      <Header>
        <ScanButton to="/scan-product">
          <img src={ScanIcon} alt="" />
          <ScanButtonText>Scan again</ScanButtonText>
        </ScanButton>
      </Header>

      <Line />

      <Content>
        <ProductDetails>
          <ProductHeader {...selectedItem} />
          <ProductDescription {...selectedItem} />
        </ProductDetails>
        <RelatedProductInfo {...selectedItem} />
      </Content>

      <Static>
        <Line />

        <Footer>
          { !isReordered && <ReOrderButton onClick={openModal}>Re-order Product</ReOrderButton> }
          { isReordered && <DoneButton onClick={redirectHomePage}>Done</DoneButton> }
        </Footer>
      </Static>
      {showModal ? <Modal showModal={showModal} setShowModal={setShowModal} {...selectedItem} customImageSize={180} setReorderes={setReorderes} /> : null}
      <ToastContainer pauseOnFocusLoss={false} hideProgressBar />
    </Container>
  );
}

const Static = styled.div`
  background-color: ${Colors.white};
  width: 100%;
  position: absolute;
  bottom: 0;
`;

const Header = styled.div`
  display: flex;
  align-items: center;
  padding: 16px;
`;

const ScanButton = styled(Link)`
  ${Mixins.helvetica};
  ${Mixins.flexRow};
  text-decoration: none !important;
  &:visited {
    color: ${Colors.darkGrey};
  }
`;

const ScanButtonText = styled.span`
  margin-left: 12px;
  margin-top: 1px;
  font-weight: bold;
`;

const Line = styled.div`
  width: 100%;
  height: 1px;
  background-color: ${Colors.lightGrey3};
`;

const Container = styled.div`
  ${Mixins.helvetica};
  ${Mixins.flexColumn};
  height: 100%;
`;

const Content = styled.div`
  ${Mixins.flexColumn};
  flex-grow: 1;
  padding: 16px;
`;

const ProductDetails = styled.div`
  ${Mixins.flexColumn};
`;

const Footer = styled.div`
  padding: 16px;
  ${Mixins.flexRow};
  justify-content: center;
`;

const ReOrderButton = styled(ButtonBase)`
  &.MuiButtonBase {
    &-root {
      ${Mixins.helvetica}
      color: ${Colors.white};
      font-size: 16px;
      font-weight: 600;
      background: ${Colors.black};
      padding: 16px 64px;
      border-radius: 24px;
      letter-spacing: 0.50px;
      &.Mui-disabled {
        background-color: ${Colors.disabledButton};
      }
    }
  }
`;

const DoneButton = styled(ButtonBase)`
  &.MuiButtonBase {
    &-root {
      ${Mixins.helvetica}
      color: ${Colors.white};
      font-size: 16px;
      font-weight: 600;
      background-color: #07B10C;
      padding: 16px 64px;
      border-radius: 24px;
      letter-spacing: 0.50px;
      &.Mui-disabled {
        background-color: ${Colors.disabledButton};
      }
    }
  }
`;
