import { productList } from '../db/products';

const getProductList = () => {
  return productList.filter(product => product.type === process.env.REACT_APP_PRODUCT_TYPE);
};

export const getProductByCode = (code) => {
  return getProductList().find(product => product.code === code);
};

export const getProductById = (id) => {
  return getProductList().find(product => product.id === id);
};

export const getProductsByCategory = (category) => {
  return getProductList().filter(product => product.category === category);
};

export const getAlternativeProductList = (category, selectedProductId) => {
  return getProductsByCategory(category).filter(product => product.id !== selectedProductId);
};
