import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import { Colors } from '../shared';
import { ProductHeader } from './ProductHeader';

function AlternativeProduct(props) {
  const { id, parentId } = props;
  const handleProductClick = () => {
    const container = document.getElementById(parentId);
    container.scrollTop = 0;
  };
  return (
    <Container>
      <ProductHeader {...props} customImageSize={90}>
        <ViewProduct onClick={handleProductClick}>
          <LinkButton to={`/result/${id}`}>View Product</LinkButton>
        </ViewProduct>
      </ProductHeader>
    </Container>
  );
}

export default AlternativeProduct;

const Container = styled.div`
  border: 1px solid ${Colors.lightGrey3};
  padding: 0 16px;
  margin-bottom: 12px;
  border-radius: 6px;
`;

const LinkButton = styled(Link)`
  color: ${Colors.black};
`;

const ViewProduct = styled.div`
  padding-top: 12px;
`;
