import React from 'react';
import styled from 'styled-components';
import Header from './Header';
import { Colors, Mixins, Sizes, Breakpoints } from '../shared/typography';

export default function Layout({ children }) {
  return (
    <LayoutContainer>
      <Header />
      <LayoutContent>
        {children}
      </LayoutContent>
    </LayoutContainer>
  );
}

const LayoutContainer = styled.div`
  ${Mixins.flexCenter}
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  overflow: hidden;
  background: ${Colors.backgroundLighter};
`;

const LayoutContent = styled.div`
  background: ${Colors.white};
  width: 100vw;
  height: 100%;
  max-width: ${Sizes.maxWidth};
  position: relative;
  box-sizing: border-box;

  @media(${Breakpoints.min}) {
    height: calc(100% - ${Sizes.contentMargin} * 3);
    margin-top: calc(${Sizes.contentMargin});
    max-width: ${Sizes.desktopMaxWidth};
  }
`;
