export function scrapDrivingLicenceData(data) {
  try {
    const dataArray = data.split(/\r?\n/);
    const result = {};

    dataArray.forEach((item) => {
      if (!item || item.length < 3 || item.substr(3) === 'NONE') { return; }

      const foundField = driverLicenseFields.find(field => field.code === item.substr(0, 3));
      const fieldName = foundField ? foundField.name : item.substr(0, 3);

      result[fieldName] = result[fieldName] || item.substr(3);
    });

    return result;
  } catch (err) {
    console.error(err, data);
    return null;
  }
}

export function normalizeDrivingLicenceData(data) {
  if (!data) { return null; }

  const copy = { ...data };

  Object.keys(copy).forEach((fieldName) => {
    if (fieldName === 'sex') {
      copy[fieldName] = copy[fieldName] === '2' ? 'Female' : 'Male';
    }

    if (fieldName === 'dateOfBirth') {
      copy[fieldName] = parseDate(copy[fieldName]);
    }

    if (fieldName === 'mailingPostalCode' || fieldName === 'residencePostalCode') {
      copy[fieldName] = `${copy[fieldName].substr(0, 5)}-${copy[fieldName].substr(5, 4)}`;
    }

    if (fieldName.toLowerCase().includes('name')) {
      copy[fieldName] = humanizeString(copy[fieldName]);
    }

    if (cityPartsFields.includes(fieldName)) {
      copy[fieldName] = humanizeString(copy[fieldName]);
    }
  });

  return copy;
}

export function parseDrivingLicenceData(result) {
  const data = normalizeDrivingLicenceData(scrapDrivingLicenceData(result));

  if (!data) { return null; }

  const name = getName(data);
  const [residenceAddress, residenceCity] = getResidenceAddress(data);
  const [mailingAddress, mailingCity] = getMailingAddress(data);

  const {
    dateOfBirth,
    sex,
    countryTerritoryOfIssuance,
    inventoryControlNumber,
    documentDiscriminator,
  } = data;

  const parsedResult = {
    name,
    dateOfBirth,
    sex,
    residenceAddress,
    residenceCity,
    mailingAddress,
    mailingCity,
    country: countryTerritoryOfIssuance,
    id: documentDiscriminator || inventoryControlNumber,
  };

  return parsedResult;
}



export function getDrivingLicenceOrderedFields(data) {
  if (!data) { return []; }

  const mapField = field => ({ field, value: data[field] });

  const handledFields = fieldsOrder
    .map(mapField)
    .filter(field => !!field.value);

  const unhandledFields = Object.keys(data)
    .filter(key => !fieldsOrder.includes(key))
    .map(mapField);

  return [...handledFields, ...unhandledFields];
}

// helpers

function parseDate(date) {
  if (!date || date.length !== 8) {
    return null;
  }

  return `${date.substr(0, 2)}/${date.substr(2, 2)}/${date.substr(4, 4)}`;
}

function humanizeStringPart(text) {
  return text && `${text.charAt(0).toUpperCase()}${text.slice(1).toLowerCase()}`;
}

function humanizeString(text) {
  return text.split(' ').map(humanizeStringPart).join(' ');
}

function getName(data) {
  const { namePrefix, firstName, middleName, lastName, nameSufix } = data;

  return [namePrefix, firstName, middleName, lastName, nameSufix].filter(Boolean).join(' ');
}

function getAddress(data, type) {
  const order = [['StreetAddress1', 'StreetAddress2'], ['City', 'PostalCode'], ['JurisdictionCode']];

  const joinPart = part => part.map(name => data[`${type}${name}`])
    .filter(Boolean)
    .join(' ');

  const ordered = order.map(joinPart).filter(Boolean);

  return [
    ordered[0] || null,
    [ordered[1], ordered[2]].filter(Boolean).join(', ') || null
  ];
}

const getResidenceAddress = data => getAddress(data, 'residence');
const getMailingAddress = data => getAddress(data, 'mailing');
