import React from 'react';
import styled from 'styled-components';

import { Mixins, PRODUCT_TYPES } from '../shared';
import { AlternativeProductList } from './AlternativeProductList';
import { AdditionalProductInfo } from './AdditionalProductInfo';
import { getAlternativeProductList } from '../services/product-service';

export const RelatedProductInfo = ({ category, id }) => {
  return (
    <Container id="overflow-container">
      {
        process.env.REACT_APP_PRODUCT_TYPE === PRODUCT_TYPES.CONSUMER &&
        (
          <AlternativeProductListContainer>
            <Label>Alternative products</Label>
            <AlternativeProductList items={getAlternativeProductList(category, id)} />
          </AlternativeProductListContainer>
        )
      }
      {
        process.env.REACT_APP_PRODUCT_TYPE === PRODUCT_TYPES.INDUSTRIAL && <AdditionalProductInfo id={id} />
      }
    </Container>
  );
};

const Container = styled.div`
  ${Mixins.flexColumn};
  height: calc(100vh - 500px);
  @media (min-width: 501px) {
    height: calc(100vh - 620px);
  }
`;

const AlternativeProductListContainer = styled.div`
  height: 100%;
`;

const Label = styled.p`
  ${Mixins.label};
  padding: 8px 0;
`;
