import {
  Barcode,
  BarcodePicker,
  CameraSettings,
  ScanSettings,
} from 'scandit-sdk';

export const Settings = {
  licenceKey: '',
  engineLocation: 'https://cdn.jsdelivr.net/npm/scandit-sdk@4.x/build',

  scanSettings: {
    enabledSymbologies: [
      Barcode.Symbology.EAN8,
      Barcode.Symbology.EAN13,
      Barcode.Symbology.UPCE,
      Barcode.Symbology.UPCA
    ],
    codeDuplicateFilter: 2000,
  },

  barcodePicker: {
    playSoundOnScan: true,
    vibrateOnScan: true,
    guiStyle: 'none',
    videoFit: BarcodePicker.ObjectFit.COVER,
    cameraSettings: {
      resolutionPreference: CameraSettings.ResolutionPreference.FULL_HD
    },
    enableCameraSwitcher: false,
  },
};

export const getScanSettings = () => new ScanSettings({ ...Settings.scanSettings });
