import React from 'react';
import BuildIcon from '@material-ui/icons/Build';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import styled from 'styled-components';

import { Mixins, Colors } from '../shared/typography';

export const AdditionalProductInfo = ({ id }) => {
  return (
    <Container>
      <Label> Additional Product Information </Label>
      <Link target="_blank" rel="noreferrer" href={`${process.env.PUBLIC_URL}/user_manual_${id}.pdf`}>
        <InstructionIcon /> User Manual
      </Link>
      <Link target="_blank" rel="noreferrer" href={`${process.env.PUBLIC_URL}/warranty_${id}.pdf`}>
        <WarrantyIcon /> Warranty
      </Link>
    </Container>
  );
};

const Container = styled.div`
  ${Mixins.flexColumn};
`;

const Label = styled.p`
  ${Mixins.label};
  padding-top: 10px;
`;

const Link = styled.a`
  border-radius: 6px;
  color: ${Colors.backgroundDark};
  display: flex;
  align-items: center;
  background-color: ${Colors.lightGrey2};
  margin-top: 12px;
  padding: 8px 16px;
`;

const InstructionIcon = styled(MenuBookIcon)`
  margin-right: 16px;
`;

const WarrantyIcon = styled(BuildIcon)`
  margin-right: 16px;
`;
