import React, { useState, useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useHistory } from 'react-router-dom';
import ScanditBarcodeScanner from 'scandit-sdk-react';
import { ScanningBy } from '@scandit/react-toolkit';
import styled from 'styled-components';

import { getProductByCode } from '../services/product-service';
import { Settings, getScanSettings } from '../shared';

export function ScanPage() {
  const scanSettings = getScanSettings();
  const [result, setResult] = useState();
  const history = useHistory();

  useEffect(() => {
    const barcode = result && result.barcodes && result.barcodes[0];
    if (!barcode) { return; }

    const scannedProduct = getProductByCode(barcode.data);
    if (!scannedProduct) {
      toast.error('Unsupported Barcode');
      return;
    }
    history.push(`/result/${scannedProduct.id}`);
  }, [result]);

  const onScan = (scanResult) => {
    setResult(scanResult);
  };

  const onScanError = err => toast.error(typeof err === 'string' ? err : 'Unexpected error');

  return (
    <ScanContent>
      <ScanditBarcodeScanner
        {...Settings.barcodePicker}
        licenseKey={Settings.licenceKey}
        engineLocation={Settings.engineLocation}
        onScan={onScan}
        onScanError={onScanError}
        scanSettings={scanSettings}
        paused={false}
        accessCamera
        guiStyle="laser"
      />
      <ScanningBy />
      <ToastContainer pauseOnFocusLoss={false} hideProgressBar />
    </ScanContent>
  );
}

const ScanContent = styled.div`
  .scandit-logo {
    display: none;
  }

  transition: opacity 0.3s;
  opacity: 1;
  width: 100%;
  height: 100%;
`;
