import React, { useState, useEffect } from 'react';
import ButtonBase from '@material-ui/core/ButtonBase';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { Colors, Mixins } from '../shared/typography';
import Logo from '../images/scandit_logo.svg';

export default function HomePage({
  title,
  description,
  scanLink = '/scan',
  scanText = 'Scan',
  Footer,
}) {
  const [disabled, setDisabled] = useState(true);

  useEffect(() => {
    if (!navigator || !navigator.mediaDevices) {
      alert('Not able to access camera on this device')
      return;
    }

    navigator.mediaDevices.getUserMedia({ video: { facingMode: 'environment' }, audio: false })
      .then((stream) => {
        stream.getTracks().forEach(track => track.stop());
        setDisabled(false);
      })
      .catch(console.error);
  }, []);

  return (
    <HomeContainer>

      <HomeLogoContainer>
        <img src={Logo} height="28px" fill={Colors.darkGrey} alt="Scandit Logo" />
      </HomeLogoContainer>

      <HomeBottomContainer>
        <Title>{title}</Title>
        <Description>{description}</Description>
        <LinkButton to={scanLink}>
          <Button focusRipple disabled={disabled}>{scanText}</Button>
        </LinkButton>

        {Footer && <Footer/>}

      </HomeBottomContainer>

    </HomeContainer>
  );
}

const HomeLogoContainer = styled.div`
  ${Mixins.flexCenter}
  flex-basis: 45%;
`;

const HomeBottomContainer = styled.div`
  ${Mixins.flexCenter}
  flex-direction: column;
  justify-content: space-between;
  flex-basis: 55%;
  justify-content: flex-end;
`;

const HomeContainer = styled.div`
  ${Mixins.flexCenter}
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 24px;
  box-sizing: border-box;
`;

const Title = styled.div`
  ${Mixins.helvetica}
  text-align: center;
  font-size: 20px;
  line-height: 24px;
  color: ${Colors.darkGrey};
  font-weight: 600;
  margin-bottom: 16px;
  letter-spacing: 0px;
`;

const Description = styled.div`
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  color: ${Colors.darkGrey};
  margin-bottom: 6px;
  letter-spacing: 0px;
`;

const LinkButton = styled(Link)`
  ${Mixins.helvetica}
  width: 100%;
  text-decoration: none !important;
  margin: 3vh 0px;
`;

const Button = styled(ButtonBase)`
  &.MuiButtonBase {
    &-root {
      ${Mixins.helvetica}
      text-align: center;
      color: ${Colors.white};
      font-size: 16px;
      line-height: 52px;
      height: 52px;
      text-align: center;
      font-weight: 600;
      background: ${Colors.black};
      width: 100%;
      border-radius: 26px;
      letter-spacing: 0.48px;
      display: block;

      &.Mui-disabled {
        background-color: ${Colors.disabledButton};
      }
    }
  }
`;
