import React from 'react';
import styled from 'styled-components';

import AlternativeProduct from './AlternativeProduct';

export function AlternativeProductList({ items }) {
  const DOM_ELEMENT_ID = 'alternative-products-container';
  return (
    <Container id={DOM_ELEMENT_ID}>
      {items.map(item => <AlternativeProduct key={item.id} {...item} parentId={DOM_ELEMENT_ID} />)}
    </Container>
  );
}

const Container = styled.div`
  height: 100%;
  overflow: auto;
`;
