import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { Colors, Breakpoints, Sizes } from '../shared/typography';
import { ReactComponent as Logo } from '../images/scandit_logo.svg';

export default function Header() {
  return (
    <HeaderContainer>
      <Link to="/">
        <Logo height="16px" fill={Colors.white} alt="Scandit Logo" />
      </Link>
    </HeaderContainer>
  );
}

const HeaderContainer = styled.div`
  display: flex;
  align-items: center;
  background: ${Colors.backgroundDark};
  padding-left: 30px;
  padding-right: 30px;
  box-sizing: border-box;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  height: ${Sizes.topBarHeight};

  a {
    height: 16px;
  }

  @media(${Breakpoints.max}) {
    display: none;
  }
`;
