import React from 'react';
import styled from 'styled-components';

import { Colors, Mixins } from '../shared';

const DEFAULT_IMG_SIZE = 120;

export const ProductHeader = ({ numberOfItems, name, shippingDays, code, image, customImageSize, children, barcodeType }) => {
  return (
    <Container>
      <Image src={image} imgSize={customImageSize || DEFAULT_IMG_SIZE} />

      <ItemInfo>
        <TextProminent>
          <Tag>{numberOfItems} Items</Tag> in Supplier Stock
        </TextProminent>

        <ItemName>{name}</ItemName>

        <ExtraInfo>
          <LabelContainer>
            <Label>Shipping</Label>
            <Text>
              {shippingDays} business days
            </Text>
          </LabelContainer>
          <LabelContainer>
            <Label>{barcodeType}</Label>
            <Text>{code}</Text>
          </LabelContainer>
        </ExtraInfo>
        { children }
      </ItemInfo>
    </Container>
  );
};

const Container = styled.div`
  ${Mixins.helvetica};
  display: flex;
  padding: 12px 0;
`;

const Image = styled.img`
  width: ${props => props.imgSize}px;
  height: ${props => props.imgSize}px;
  margin-right: 20px;
  border: 1px solid ${Colors.black};
`;

const ItemInfo = styled.div``;

const Text = styled.p`
  margin: 0px;
  font-size: 12px;
  color: ${Colors.lightGrey};
`;

const TextProminent = styled.p`
  margin: 0px;
  font-size: 14px;
  color: ${Colors.lightGrey};
`;

const Tag = styled.span`
  ${Mixins.labelTag}
`;

const ItemName = styled.p`
  ${Mixins.helvetica}
  margin: 0px;
  font-size: 16px;
  font-weight: bold;
  padding: 8px 0;
  color: ${Colors.black};
`;

const ExtraInfo = styled.div`
  ${Mixins.flexRow};
  justify-content: space-between;
`;

const LabelContainer = styled.div`
  ${Mixins.flexColumn};
  margin-right: 15px;
`;

const Label = styled.p`
  ${Mixins.label};
  padding: 6px 0;
`;
