import { Typography } from '@scandit/react-toolkit';

export const Colors = {
  ...Typography.Colors,
  lightGrey3: '#C7C7C7'
};

export const Mixins = {
  ...Typography.Mixins,

  flexColumn: `
    display: flex;
    flex-direction: column;
  `,

  label: `
    margin: 0px;
    font-size: 14px;
    font-weight: bold;
  `,

  flexRow: `
    display: flex;
    flex-direction: row;
  `,

  labelTag: `
    background-color: ${Colors.lightGrey};
    color: ${Colors.white};
    border-radius: 2px;
    padding: 1px 6px;
  `
};

export const { Breakpoints, Sizes } = Typography;
