import React from 'react';
import styled from 'styled-components';

import { Colors } from '../shared';

export function HomeFooter() {
  return (
    <Info>
      Learn more on <InfoLink target="_blank" rel="noopener" href="https://scandit.com">scandit.com</InfoLink>
      <br />
      <InfoLink target="_blank" rel="noreferrer" href={`${process.env.PUBLIC_URL}/allbarcodes.pdf`}>Download</InfoLink> barcode samples
    </Info>
  );
}

const Info = styled.div`
  text-align: center;
  display: inline-block;
  color: ${Colors.mediumGrey};
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0px;
`;

const InfoLink = styled.a`
  color: ${Colors.mediumGrey};
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0px;
  

  &:visited {
    color: ${Colors.mediumGrey};
  }

  &:hover {
    color: ${Colors.black};
  }
`;
